<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
      <el-form-item prop="query">
        <el-input v-model="filters.query" placeholder="请输入计划单号" />
      </el-form-item>
        <el-form-item prop="query">
        <el-input v-model="filters.query" placeholder="请输入车牌号" />
      </el-form-item>
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="输入分类" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder="输入品名"/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="输入材质" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="输入规格" />
      </el-form-item>
      <el-form-item prop="date">
      <el-date-picker
      v-model="value1"
      type="datetimerange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
      </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus">创建出库单</el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="计划单号" width="150">
    <template slot-scope="scope">
    <span>{{ scope.row.jhdh }}</span>
    </template>
  </el-table-column>
  <el-table-column label="出库时间" width="100">
    <template slot-scope="scope">
    <span>{{ scope.row.date }}</span>
    </template>
  </el-table-column>
    <el-table-column label="仓库" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="分类" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="钢厂" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.changjia }}</span>
      </template>
    </el-table-column>
      <el-table-column label="品名" width="80" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="材质" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="规格" width="80">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="重量" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.zhongliang }}</span>
         </template>
        </el-table-column>
        <el-table-column label="件数" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="钢号" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.ganghao }}</span>
          </template>
        </el-table-column>
				<el-table-column label="公司" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.huowei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位数量" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.hwsl }}</span>
          </template>
        </el-table-column>
				<el-table-column label="发货员" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.fahuoyuan }}</span>
          </template>
        </el-table-column>
				<el-table-column label="备注" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
        <el-table-column label="出库车号" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.ckch }}</span>
          </template>
        </el-table-column>
    <el-table-column label="操作" width="280">
    <template slot-scope="scope">
    <el-button
      size="mini"
      type="primary"
      @click="handlePrint(scope.$index, scope.row)"><i class="el-icon-edit"></i>打印</el-button>
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
   </template>
    </el-table-column>
  </el-table>

  <!--对话框-->
  <el-dialog :title="form && form.cangku ? '编辑' : '新增' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="仓库" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="分类" prop="fenlei">
        <el-input v-model="form.name" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="钢厂" prop="fenlei">
        <el-input v-model="form.changjia" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="品名" prop="fenlei">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="材质" prop="fenlei">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="规格" prop="fenlei">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="同行单数量" prop="fenlei">
        <el-input v-model="form.txdsl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="件数" prop="fenlei">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="车皮号" prop="fenlei">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false">取消</el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
    </div>
  </el-dialog>
  <el-dialog :title="form1 ? '创建出库单' : '新增' " :visible.sync="form1Visible" width="60%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" label-width="100px" :rules="rules" ref="dynamicValidateForm">
  <el-form-item>
  <el-col :span="8">
       <el-input v-model="dynamicValidateForm.shuliang" placeholder="请输入计划单号"/>
    </el-col>
    <el-col :span="4">
       <el-button type="primary" :loading="formLoading">查询</el-button>
    </el-col>
  </el-form-item>
  <el-form-item>
  <el-col :span="5">
    <el-input v-model="dynamicValidateForm.gongsi" placeholder="请输入公司"/>
  </el-col>
  <el-col :span="5">
       <el-input v-model="dynamicValidateForm.cph" placeholder="请输入车牌号"/>
    </el-col>
    <el-col :span="5">
      <el-input v-model="dynamicValidateForm.fhy" placeholder="请输入发货员"/>
    </el-col>
    <el-col :span="5">
      <el-input v-model="dynamicValidateForm.beizhu" placeholder="备注"/>
    </el-col>
  </el-form-item>
  <el-form-item
    v-for="(domain, index) in dynamicValidateForm.domains"
    :label="'第'+index+'行' "
    :key="domain.key"
    :prop="'domains.' + index + '.value'">
    <el-col :span="12">
    <el-select v-model="domain.value" clearable  placeholder="请选择:分类/品名/材质/规格/总数量/总件数/均值" style="width:350px;">
    <el-option
     v-for="item in options"
     :key="item.value"
     :label="item.label"
     :value="item.value">
     </el-option>
     </el-select>
    </el-col>
    <el-col :span="4">
    <el-input v-model="domain.shuliang" placeholder="输入货号(1,2)"/>
    </el-col>
    <el-col :span="4">
        <el-input v-model="domain.jianshu" placeholder="输入件数(10,20)"></el-input>
    </el-col>
    <el-col :span="4">
        <el-button @click.prevent="removeDomain(domain)">删除</el-button>
    </el-col>
  </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="addDomain">新增行</el-button>
    <el-button @click.native="form1Visible = false">取消</el-button>
    <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
  </div>
  </el-dialog>

  <el-dialog :title="form2 ? '打印出库单' : '新增' " :visible.sync="form2Visible" width="60%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" label-width="100px" :rules="rules" ref="dynamicValidateForm" id="printTest">
  <el-form-item>
  <el-col :span="4">
    <el-input v-model="form2.jhdh"/>
  </el-col>
  <el-col :span="4">
       <el-input v-model="form2.cangku" />
  </el-col>
  <el-col :span="4">
    <el-input v-model="form2.gongsi" />
  </el-col>
  <el-col :span="4">
       <el-input v-model="form2.ckch"/>
    </el-col>
    <el-col :span="4">
      <el-input v-model="form2.fahuoyuan"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="建材/螺纹钢/Q235B/18(-3-5)/能投/66.89/20" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="2"/>
    </el-col>
    <el-col :span="4">
        <el-input value="20"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="建材/工字钢/HRB500E/20*9/能投/888.925/190" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="1"/>
    </el-col>
    <el-col :span="4">
        <el-input value="10"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="建材/卷钢/HRB400E/20*9/能投/998.925/190" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="3"/>
    </el-col>
    <el-col :span="4">
      <el-input value="30"/>
    </el-col>
  </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click.native="form2Visible = false">取消</el-button>
    <el-button type="primary" v-print="'#printTest'">打印</el-button>
  </div>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '请输入仓库名称',trigger: 'blur'}],
  fenlei: [{required: true,message: '请输入分类名称',trigger: 'blur'}]
}//rules
export default {
name: 'storeOutput',
data(){
    return {
    //每页数量
    size: 20,
    //查询条件
    filters: {},
    //页面数据
    rows: [],
    //页面载入状态
    pageLoading: false,
    //表单数据
    form: {},
    form1: {},
    form2: {},
    //验证规则
    rules: rules,
    //对话框隐藏状态
    formVisible: false,
    form1Visible: false,
    form2Visible: false,
    //表单提交状态
    formLoading: false,
    value: [],
    options: [{
        value: '建材/螺纹钢/Q235B/18(-3-5)/能投/66.89/20/10',
        label: '建材/螺纹钢/Q235B/18(-3-5)/能投/66.89/20/10'
      },{
       value: '建材/工字钢/HRB500E/20*9/能投/66.89/20/10',
       label: '建材/工字钢/HRB500E/20*9/能投/66.89/20/10'
       }],
       value: '',
       dynamicValidateForm: {
             domains: [{
               value: '',
               shuliang: '',
               jianshu: '',
             }],
             leibie: '',
             shulang: '',
             jianshu: ''
           }
    }//return
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('outStore/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/store/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/store/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form1 = {};
    this.form1Visible = true;
   },
  handleAddList(){
     alert("功能开发中。。。");
    },
    addDomain() {
         this.dynamicValidateForm.domains.push({
           value: '',
           key: Date.now()
         });
       },
     removeDomain(item) {
         var index = this.dynamicValidateForm.domains.indexOf(item)
         if (index !== -1) {
           this.dynamicValidateForm.domains.splice(index, 1)
         }
       },
       handlePrint(index, row){
         this.form2 = Object.assign({}, row);
         this.form2Visible = true;
        },
  }//methods
}//export default
</script>

<style scoped>
</style>



