import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Login from '@/pages/Login'
import Main from '@/pages/Main'
import StoreInput from '@/pages/StoreInput'
import StoreOrder from '@/pages/StoreOrder'
import StoreOutput from '@/pages/StoreOutput'
import StoreManage from '@/pages/StoreManage'
import StoreReport from '@/pages/StoreReport'
import Member from '@/pages/Member'
import MemberAuth from '@/pages/MemberAuth'
import MemberPassManage from '@/pages/MemberPassManage'
import Mine from '@/pages/Mine'

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/main',
      name: '库存管理',
      component: Main,
      iconCls: 'fa fa-user-circle-o',
      children:[
      {
            path:'/store/manage',
            name:'库存统计',
            component:StoreManage,
      },
      {
            path:'/store/report',
            name:'库存报表',
            component:StoreReport,
      },
     {
         path:'/store/input',
         name:'入库管理',
         component:StoreInput,
     },
     {
         path:'/store/order',
         name:'计划单',
         component:StoreOrder,
     },
     {
         path:'/store/output',
         name:'出库管理',
         component:StoreOutput,
     }
   ]
   //children这是嵌套的部分
 },
 {
   path: '/main',
   name: '用户管理',
   component: Main,
   iconCls: 'fa fa-user-circle-o',
   children:[
  {
      path:'/user/member',
      name:'用户列表',
      component:Member,
  },
  {
      path:'/user/auth',
      name:'权限管理',
      component:MemberAuth,
  },
  {
      path:'/user/passManage',
      name:'密码管理',
      component:MemberPassManage,
  }
]
//children这是嵌套的部分
},
  ]
})



