<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;width: 100%;">
    <el-form :inline="true" :model="filters">
    <el-form-item prop="gongsi">
      <el-input v-model="filters.gongsi" placeholder="输入公司" />
    </el-form-item>
    <el-form-item prop="gongsi">
      <el-input v-model="filters.changjia" placeholder="输入厂家" />
    </el-form-item>
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="输入分类" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder="输入品名"/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="输入材质" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="输入规格" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search">查询</el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="500" stripe border highlight-current-row v-loading="pageLoading">
    <el-table-column label="仓库" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="公司" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.gongsi }}</span>
      </template>
    </el-table-column>
    <el-table-column label="厂家" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.changjia }}</span>
      </template>
    </el-table-column>
    <el-table-column label="分类" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
      <el-table-column label="品名" width="80" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="材质" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="规格" width="80">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
      <el-table-column label="总数量" width="80" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.zsl }}</span>
       </template>
      </el-table-column>
      <el-table-column label="总件数" width="80">
        <template slot-scope="scope">
        <span>{{ scope.row.zjs }}</span>
        </template>
      </el-table-column>
        <el-table-column label="剩余数量" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.sysl }}</span>
         </template>
        </el-table-column>
        <el-table-column label="剩余件数" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.syjs }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.huowei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位数量" width="150">
          <template slot-scope="scope">
          <span>{{ scope.row.hwsl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位件数" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.hwjs }}</span>
          </template>
        </el-table-column>
  </el-table>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '请输入仓库名称',trigger: 'blur'}],
  fenlei: [{required: true,message: '请输入分类名称',trigger: 'blur'}]
}//rules
export default {
name: 'storeManage',
data(){
    return {
    //每页数量
    size: 20,
    //查询条件
    filters: {},
    //页面数据
    rows: [],
    //页面载入状态
    pageLoading: false,
    //表单数据
    form: {},
    //验证规则
    rules: rules,
    //对话框隐藏状态
    formVisible: false,
    //表单提交状态
    formLoading: false,
    value1: ''
    }
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/store/manage', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  }
  }//methods
}//export default
</script>

<style scoped>
</style>



