<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
    <el-form-item prop="fenlei">
      <el-input v-model="filters.fenlei" placeholder="输入分类" />
    </el-form-item>
    <el-form-item prop="pinming">
      <el-input v-model="filters.pinming" placeholder="输入品名"/>
    </el-form-item>
    <el-form-item prop="caizhi">
      <el-input v-model="filters.caizhi" placeholder="输入材质" />
    </el-form-item>
    <el-form-item prop="guige">
      <el-input v-model="filters.guige" placeholder="输入规格" />
    </el-form-item>
    <el-form-item prop="gongsi">
      <el-input v-model="filters.gongsi" placeholder="输入公司" />
    </el-form-item>
    <el-form-item prop="changjia">
      <el-input v-model="filters.changjia" placeholder="输入厂家" />
    </el-form-item>
    <el-form-item prop="date">
    <el-date-picker
    v-model="value1"
    type="datetimerange"
    range-separator="至"
    start-placeholder="开始日期"
    end-placeholder="结束日期">
    </el-date-picker>
    </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus">添加</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAddList" icon="el-icon-plus">模板导入</el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="uid" width="80">
    <template slot-scope="scope">
    <span>{{ scope.row.uid }}</span>
    </template>
  </el-table-column>
  <el-table-column label="入库时间" width="100">
    <template slot-scope="scope">
    <span>{{ scope.row.date }}</span>
    </template>
  </el-table-column>
    <el-table-column label="仓库" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="分类" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="钢厂" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.changjia }}</span>
      </template>
    </el-table-column>
      <el-table-column label="品名" width="100" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="材质" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="规格" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="同行单数量" width="100" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.txdsl }}</span>
         </template>
        </el-table-column>
        <el-table-column label="件数" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车皮号" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.cph }}</span>
          </template>
        </el-table-column>
        <el-table-column label="复核重量" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.rkfhzl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="磅差" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.bangcha }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.huowei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="货位数量" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.hwsl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="公司" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="收货人" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.shr }}</span>
          </template>
        </el-table-column>
        <el-table-column label="核对状态" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.zhuangtai }}</span>
          </template>
        </el-table-column>
        <el-table-column label="备注" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
        <el-table-column label="均值" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.junzhi }}</span>
          </template>
        </el-table-column>
    <el-table-column label="操作" width="270">
    <template slot-scope="scope">
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
    <el-button
         size="mini"
         type="primary"
         @click="handleEdit1(scope.$index, scope.row)"><i class="el-icon-edit"></i>移库</el-button>
   </template>
    </el-table-column>
  </el-table>

  <!--对话框-->
  <el-dialog :title="form && form.cangku ? '编辑' : '新增' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="仓库" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="分类" prop="fenlei">
        <el-input v-model="form.fenlei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="钢厂" prop="fenlei">
        <el-input v-model="form.changjia" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="品名" prop="fenlei">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="材质" prop="fenlei">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="规格" prop="fenlei">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="同行单数量" prop="fenlei">
        <el-input v-model="form.txdsl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="件数" prop="fenlei">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="车皮号" prop="fenlei">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="货位" prop="fenlei">
        <el-input v-model="form.huowei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="公司" prop="fenlei">
        <el-input v-model="form.gongsi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="审核人" prop="fenlei">
        <el-input v-model="form.shr" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false">取消</el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
    </div>
  </el-dialog>

  <!--对话框-->
  <el-dialog :title="form1 && form1.cangku ? '移库' : '新增' " :visible.sync="form1Visible" :close-on-click-modal="false">
    <el-form :model="form1" label-width="60px" :rules="rules" ref="form1">
      <el-row>
      <el-col :span="5">
      <el-form-item label="仓库" prop="cangku">
        <el-input v-model="form1.cangku" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="分类" prop="fenlei">
        <el-input v-model="form1.fenlei" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="钢厂" prop="fenlei">
        <el-input v-model="form1.changjia"disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="品名" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.pinming" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="5">
      <el-form-item label="材质" prop="fenlei">
        <el-input v-model="form1.caizhi" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="规格" prop="fenlei">
        <el-input v-model="form1.guige" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="货位" prop="fenlei">
        <el-input v-model="form1.huowei" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="车皮号" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.cph" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="7">
      <el-form-item label="公司" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.gongsi" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="同行数量" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.txdsl" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="件数" prop="fenlei" label-width="80px">
        <el-input v-model="form1.fzjl" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="7">
      <el-form-item label="移入公司" prop="fenlei" label-width="80px">
        <el-input v-model="form1.cph1" />
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="移入数量" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.txdsl1" />
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="件数" prop="fenlei" label-width="80px">
        <el-input v-model="form1.fzjl1" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="form1Visible = false">取消</el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
    </div>
  </el-dialog>
  <!--对话框-->
  <el-dialog :title="form2 ? '模板导入' : '导入' " :visible.sync="form2Visible" :close-on-click-modal="false" width="30%">
    <el-upload
      class="upload-demo"
      action="https://jsonplaceholder.typicode.com/posts/"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      multiple
      :limit="1"
      accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
      :on-exceed="handleExceed"
      :file-list="fileList">
      <el-button size="small" type="primary">点击上传</el-button>
      <div slot="tip" class="el-upload__tip">只能上传execl文件，且不超过50M</div>
    </el-upload>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '请输入仓库名称',trigger: 'blur'}],
  fenlei: [{required: true,message: '请输入分类名称',trigger: 'blur'}]
}//rules
export default {
name: 'storeInput',
data(){
    return {
    //每页数量
    size: 20,
    //查询条件
    filters: {},
    //页面数据
    rows: [],
    //页面载入状态
    pageLoading: false,
    //表单数据
    form: {},
    form1: {},
    form2: {},
    //验证规则
    rules: rules,
    //对话框隐藏状态
    formVisible: false,
    form1Visible: false,
    form2Visible: false,
    fileList: [],
    //表单提交状态
    formLoading: false
    }
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/inStore/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
   handleEdit1(index, row){
     this.form1 = Object.assign({}, row);
     this.form1Visible = true;
    },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/inStore/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/inStore/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form = {};
    this.formVisible = true;
   },
  handleAddList(){
  this.form2Visible = true;
    },
    handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      }
  }//methods
}//export default
</script>

<style scoped>
</style>



