import axios from './http'

var api = {}

api.get = function (url, params={}) {
  return new Promise((resolve,reject) => {
    axios.get(url, params).then((res) => {
        if (res.status === 200) {
            resolve(res)
        }
    }).catch((error) => {
        reject(error)
    })
  })
}

api.post = function (url, params={}) {
   return new Promise((resolve,reject) => {
    axios.post(url, params).then((res) => {
            if (res.status === 200) {
                resolve(res)
                }
        }).catch((error) => {
        reject(error)
    })
    })
}

export default () => {
    window.api = api;
};



