<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
    <el-form-item prop="query">
      <el-input v-model="filters.query" placeholder="输入计划单号" />
    </el-form-item>
    <el-form-item prop="query">
      <el-input v-model="filters.query" placeholder="输入创建日期" />
    </el-form-item>
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="输入分类" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder="输入品名"/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="输入材质" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="输入规格" />
      </el-form-item>
      <el-form-item prop="date">
      <el-date-picker
      v-model="value1"
      type="datetimerange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
      </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus">创建计划单</el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="计划单号" width="150">
    <template slot-scope="scope">
    <span>{{ scope.row.jhdh }}</span>
    </template>
  </el-table-column>
    <el-table-column label="仓库" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="分类" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="品名" width="100" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="材质" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="规格" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="数量" width="100" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.shuliang }}</span>
         </template>
        </el-table-column>
        <el-table-column label="件数" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="公司" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.cph }}</span>
          </template>
        </el-table-column>
        <el-table-column label="备注" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
    <el-table-column label="操作" width="180">
    <template slot-scope="scope">
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
   </template>
    </el-table-column>
  </el-table>

  <!--对话框-->
  <el-dialog :title="form && form.cangku ? '编辑' : '新增' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="计划单号" prop="jhdh">
        <el-input v-model="form.jhdh"/>
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="仓库" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="分类" prop="fenlei">
        <el-input v-model="form.fenlei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="品名" prop="pinming">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="材质" prop="caizhi">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="规格" prop="guige">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="数量" prop="shuliang">
        <el-input v-model="form.shuliang" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="件数" prop="fzjl">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="公司" prop="gongsi">
        <el-input v-model="form.gongsi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="车牌号" prop="cph">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="备注" prop="beizhu">
        <el-input v-model="form.beizhu" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false">取消</el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
    </div>
  </el-dialog>
  <!--对话框-->
  <el-dialog :title="form1 && form1.cangku ? '编辑' : '创建计划单' " :visible.sync="dialogTableVisible" width="80%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="70px" class="demo-dynamic">
  <el-row>
  <el-col :span="4">
  <el-form-item label="仓库" prop="cangku">
    <el-input v-model="form1.cangku" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="公司" prop="gongsi">
    <el-input v-model="form1.gongsi" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="车牌号" prop="cph">
    <el-input v-model="form1.cph" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="备注" prop="beizhu">
    <el-input v-model="form1.beizhu" />
  </el-form-item>
  </el-col>
  </el-row>
  <el-form-item
    v-for="(domain, index) in dynamicValidateForm.domains"
    :label="'第'+index+'行' "
    :key="domain.key"
    :prop="'domains.' + index + '.value'">
    <el-col :span="10">
    <el-select v-model="domain.value" placeholder="请选择:分类/品名/材质/规格/可用数量/可用件数" style="width:400px;">
    <el-option
     v-for="item in options"
     :key="item.value"
     :label="item.label"
     :value="item.value">
     </el-option>
     </el-select>
    </el-col>
    <el-col :span="3">
    <el-input v-model="domain.shuliang" placeholder="请输入数量"></el-input>
    </el-col>
    <el-col :span="3">
        <el-input v-model="domain.jianshu" placeholder="请输入件数"></el-input>
    </el-col>
    <el-col :span="4">
        <el-button @click.prevent="removeDomain(domain)">删除</el-button>
    </el-col>
  </el-form-item>
 </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="addDomain">新增行</el-button>
      <el-button @click.native="dialogTableVisible = false">取消</el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading">提交</el-button>
    </div>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '请输入仓库名称',trigger: 'blur'}],
  fenlei: [{required: true,message: '请输入分类名称',trigger: 'blur'}]
}//rules
export default {
name: 'storeInput',
data(){
    return {
    //每页数量
    size: 20,
    //查询条件
    filters: {},
    //页面数据
    rows: [],
    //页面载入状态
    pageLoading: false,
    //表单数据
    form: {},
    form1: {},
    //验证规则
    rules: rules,
    //对话框隐藏状态
    formVisible: false,
    dialogTableVisible: false,
    //表单提交状态
    formLoading: false,
    options: [{
          value: '建材/卷钢/HRB500E/20*9/998.925/190',
          label: '建材/卷钢/HRB500E/20*9/888.925/190'
        }, {
          value: '建材/工字钢/HRB500E/20*9/888.925/190',
          label: '建材/工字钢/HRB500E/20*9/888.925/100'
        }, {
          value: '建材/螺纹钢/Q235B/18(-3-5)/888.925/190',
          label: '建材/螺纹钢/Q235B/18(-3-5)/888.925/100'
        }],
    value: '',
    dynamicValidateForm: {
          domains: [{
            value: '',
            shuliang: '',
            jianshu: '',
          }],
          leibie: '',
          shulang: '',
          jianshu: ''
        }
    }//return
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/storeOrder/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/inStore/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/inStore/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form1 = {};
    this.dialogTableVisible = true;
   },
   addDomain() {
        this.dynamicValidateForm.domains.push({
          value: '',
          key: Date.now()
        });
      },
    removeDomain(item) {
        var index = this.dynamicValidateForm.domains.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.domains.splice(index, 1)
        }
      }
  }//methods
}//export default
</script>

<style scoped>
</style>



