<template>
 <el-form class="login-container" ref="AccountForm" :model="account":rules="loginRules" label-position="left">
  <h3>库存管理系统</h3>
  <el-form-item prop="username">
   <el-input v-model="account.username" type="text" placeholder="账号"></el-input>
  </el-form-item>
  <el-form-item prop="password">
   <el-input v-model="account.password" type="password" placeholder="密码"></el-input>
  </el-form-item>
  <el-checkbox v-model="checked" checked>记住密码</el-checkbox>
  <el-form-item>
    <el-button @click.native.prevent="login" :loading="logining" type="primary">登录</el-button>
    <el-button type="primary" class="resetBtn" @click.native.prevent="reset">重置</el-button>
   </el-form-item>
 </el-form>
</template>

<script>
 export default {
  name: "login",
  data () {
   return {
    account: {
     username: '',
     password: ''
    },
    loginRules: {
     username: [{required: true, message: '请输入账号', trigger: 'blur'}],
     password: [{required: true,message: '请输入密码', trigger: 'blur'}],
    },
    checked: true,
    logining: false
   };
  },
  methods: {
   login() {
    this.$refs.AccountForm.validate((valid) => {
     if (valid) {
      this.logining = true;
      var params = {
      "name": this.account.username,
      "pwd": this.account.password
      };
			api.post('/login',params).then((res) => {
        console.log(res);
        if(res.data.success){
           if(res.data.data.message =="success"){
             this.$router.push('/main');
           }else{
             alert("用户名或密码错误");
             this.logining = false;
           }
        }
      })
			}
			})
    },
    reset () {
     this.$refs.AccountForm.resetFields()
    },
   }//methods
  }//export default
</script>
<style scoped>
 body{
  background: #DFE9FB;
 }
.login-container {
 width:350px;
 margin-left:35%;
 border: 1px solid #d3d3d3;
 box-sizing: border-box;
 padding: 10px 30px;
 border-radius: 5px;
}
 .el-button {
  width:100%;
  box-sizing: border-box;
  margin: 10px 0;
 }
</style>



